@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.iconSwitch__icon {
  font-size: 2rem;
  cursor: pointer;
}

.shopCard__card {
  width: 26%;
  padding: 3%;
  margin: 1% 0;
  background-color: rgb(245, 245, 245);
  box-shadow: 5px 5px 5px 0px rgba(0,0,0,0.33);
}

.shopCard__item {
  width: 100%;
}

.shopCard__img {
  object-fit: cover;
  display: block;
  /* width: 200px; */
  height: 250px;
  margin-left: auto;
  margin-right: auto;
  background-color: rgb(218, 218, 218);
}

.shopCard__title {
  text-transform: uppercase;
}

.shopCard__inner {
  display: flex;
  justify-content: space-between;
}

.shopCard__color,
.shopCard__price,
.shopCard__btn {
  font-size: 1.5rem;
}

.shopCard__price::before {
  content: "$";
}

.shopCard__btn {
  width: 38%;
  cursor: pointer;
  padding: 10px;
  color: red;
  border: 1px dashed red;
  border-radius: 20px;
}

.cardView__container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}


.shopItem__card {
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgb(245, 245, 245);
  box-shadow: 2px 2px 2px 0px rgba(0,0,0,0.33);

}

.shopItem__item {
  width: 22%;
}

.shopItem__item:last-child {
  width: 10%;
}

.shopItem__img {
  display: block;
  width: 100px;
  height: 100px;
  background-color: rgb(218, 218, 218);
}

.shopItem__title {
  text-transform: uppercase;
}

.shopItem__color,
.shopItem__price,
.shopItem__btn {
  font-size: 1.5rem;
}

.shopItem__price::before {
  content: "$";
}

.shopItem__btn {
  cursor: pointer;
  padding: 10px;
  color: red;
  border: 1px dashed red;
  border-radius: 20px;
}

.listView__container div{
  margin-bottom: 10px;
}

.listView__container div:last-child{
  margin-bottom: 0;
}

.store__container {
  padding: 30px;
  width: 1600px;
  margin-left: auto;
  margin-right: auto;
}

.store__icon {
  padding: 10px 30px;
  display: flex;
  justify-content: flex-end;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

